html,body{
    margin:0;
    padding:0;
    
}
#root{
    height:100vh;
}
.outerContainer{
    display: flex;
    justify-content: center;
    height: 100vh;
    text-align: center;
    
    background: #2b5876; 
    background: linear-gradient(to bottom, #4e4376, #2b5876);

    align-items: center;
}
.innerContainer{
    width: 40%;
    height: 44%;
    background-color: black;
    text-align: center;
    border-radius: 5px;
}

.namefield{
    margin-top: 3vh;
    border:none;
    padding: 12px 18px;
    width: 70%;
    outline: none;
    font-size:20px;
}
.roomfield{
    margin-top: 3vh;
    border:none;
    padding: 12px 18px;
    width: 70%;
    outline: none;
    font-size:20px;
}
.join-btn{
    margin-top: 3vh;
