}
@media screen and (max-width:600px){
    .innerContainer{
        width: 90%;
        height: 47%;
        background-color: black;
        text-align: center;
    }
}
#footer1{
    margin-top:30px ;
}
.infobar{
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgb(43, 118, 179);
    height: 10%;
    width: 100%;
    border-radius: 0px 5px 0px 0px;
}

.left-container{
    flex:0.9 1;
    display :flex;
    align-items: center;
    justify-content: flex-start;
    margin-left: 5px;
    color: white;
    font-size: 20px;
}
.rigth-container{
    flex:0.5 1;
    display :flex;
    justify-content: flex-end;
    margin-right: 5px;
    color: white;
}
.pl{
    padding-left: 20px;
}
.mr{
    margin-right: 20px;
}
.copy-btn{
    outline: none;
    cursor: pointer;
    border: none;
    background:none;
}

.form{
    display: flex;
    background-color: rgb(235, 235, 235);
    height: 10%;
    border-bottom: 1px solid rgb(184, 184, 184);
    border-right: 1px solid rgb(184, 184, 184);;
}
.input-bar{
    border:none;
    padding: 1%;
    width: 80%;
    font-size: 1.2rem;
    border-radius: 25px;
    height: 50%;
    margin-left: 7px;
    margin-top: 6px;
}
input:focus,textarea:focus,select:focus{
    outline: none;
}
input::-webkit-input-placeholder{
    opacity: 80%;
    font-size: 17px;
}
input:-ms-input-placeholder{
    opacity: 80%;
    font-size: 17px;
}
input::-ms-input-placeholder{
    opacity: 80%;
    font-size: 17px;
}
input::placeholder{
    opacity: 80%;
    font-size: 17px;
}
.send-btn{
    width: 10%;
    border-radius: 0px 0px 5px;
    outline: none;
    border: none;
}
.attach-btn{
    width: 10%;
    outline: none;
    border: none;
}
.attach-icon{
    height:40%;
    width:40%;
}
.send-icon{
    height:50%;
    width:50%;
    cursor: pointer;
}
.image-upload{
    width: 10%;
    display: flex;
    align-items: center;
    justify-content: center;
}
.image-upload > input
{
    display: none;
}

.image-upload img
{
    margin-left:30px ;
    width: 45%;
    height: 60%;
    cursor: pointer;
}
@media screen and (max-width:600px){
    .input-bar{
        margin-top: 12px;
    }
    .image-upload img
    {
        margin-left:7px ;
        width: 75%;
        height: 75%;
        cursor: pointer;
    }
    .send-icon{
        height:100%;
        width:90%;
    }
}
.side-bar-container{
    display: flex;
    flex-direction: column;
    flex: 0.35 1;
    background-color: rgb(0, 0, 0);
    width: 30%;
    height: 90%;
    color: rgb(224, 224, 224);
    text-align: center;
    border-radius: 5px 0px 0px 5px;
}
.pt1-20{
    padding-top: 15px;
}
.users{
    margin: auto;
    font-size: 1.6rem;
  }
.head{
    display: flex;
    font-size: 23px;
    justify-content: center;
    align-items: center;
    border-bottom: 1px solid rgb(112, 112, 112);
    height: 10%;
}
.icon{
    color: rgb(33, 163, 65);
    height: 7px;
    width: 7px;
    padding: 10px;
}
.menu{
    display: flex;
    font-size: 22px;
    justify-content: center;
    align-items: center;
    border-radius: 20px 0px 0px 0px;
    height: 10%;
}
.icon-grp{
    height: 40px;
    width: 40px;
}
.msg-icon{
    height: 30px;
    width: 30px;
    padding:5px;
}
.menu-2{
    display: flex;
    align-items: center;
    justify-content:center ;
    height: 8%;
}
.scrollbar1 {
    overflow: scroll;
    overflow-x: hidden;
    flex: auto;
  }
  .scrollbar1::-webkit-scrollbar {
    width: 6px;
    height: 6px;
  }
  
  .scrollbar1::-webkit-scrollbar-thumb {
    border-radius: 5px;
    -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.1);
    background: rgba(0, 0, 0, 0.2);
  }
  
  .scrollbar1-primary1::-webkit-scrollbar {
    background-color: #000000;
  }
  .scrollbar1-primary1 {
    scrollbar-color: #000000 #050505;
  }
  .scrollbar1-primary1::-webkit-scrollbar-thumb {
    border-radius: 10px;
    -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.1);
    background-color: #000000;
  }

  @media screen and (max-width:600px){
    .side-bar-container{
        display: block;
        background-color: rgb(0, 0, 0);
        width: 100%;
        height: 100%;
        color: rgb(224, 224, 224);
        text-align: center;
        border-radius: 5px 0px 0px 5px;
    }
}
body{
    font-family: 'Antic', sans-serif;
}
.outer-container{
    justify-content: center;
    align-items: center;
    background: #314755; 
background: linear-gradient(to bottom, #314755,#26a0da); 

    height: 100vh;
    display: flex;
}

.inner-container{
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    background-color: rgb(255, 255, 255);
    height: 90%;
    width: 65%;
    border-radius: 0px 20px 20px 0px;
}

@media screen and (max-width:600px){
    .outer-container {
        display: block;
      }
      .inner-container{
        height: 100%;
        width: 100%;
    }
}
.messages{
    background-image: url(/chat-app-1/static/media/back.0929ca7a.png);
    background-size: cover;
    height: 75%;
    overflow:auto;
    flex:auto;
}
.scrollbar {
    overflow:auto;
  }
  
  .scrollbar::-webkit-scrollbar {
    width: 7px;
    height: 6px;
  }
  
  .scrollbar::-webkit-scrollbar-thumb {
    border-radius: 5px;
    -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.1);
    background: rgba(0, 0, 0, 0.2);
  }
  
  .scrollbar-primary::-webkit-scrollbar {
    scroll-margin: auto;
  }
  .scrollbar-primary {
    scrollbar-color: #76c5f3;
  }
  .scrollbar-primary::-webkit-scrollbar-thumb {
    border-radius: 10px;
    -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.1);
    background-color: #5ebdfc;
  }
.message-cointainer{
    display: flex;
    justify-content: flex-end;
    padding: 0 5%;
    margin-top: 3px;
}
.sentText{
    display: flex;
    align-items: center;
    color: rgb(129, 129, 129);
    letter-spacing: 0.3px;
}
.imageBox{
    margin-top:5px;
    display:flex;
    justify-content: center;
    align-items: center;
    height: 150px;
    width: 130px;
}
.messageBox {
    background: #f3f3f3;
    border-radius: 25px;
    color: white;
    display: inline-block;
    max-width: 70%;
    padding: 0px 20px;
    margin-top: 2px;
}
.messageText{
    width: 100%;
    letter-spacing: 0;
    float: left;
    font-size: 1.1em;
    word-wrap: break-word;
}
